import React, { useState } from 'react'
import styles from './index.module.less'
import Header from '../../components/Header'
import { Button, Dialog, Input } from 'antd-mobile'
export default function Qrcode() {
  const [text, setText] = useState('')
  const [size, setSize] = useState(100)
  const submit = () => {
    if (text === '') {
      Dialog.alert({
        content: '内容不能为空'
      })
    } else {
      window.open(`https://api.oick.cn/qrcode/api.php?text=${text}&size=${size}`)
    }
  }
  return (
    <div className={styles.container}>
      <Header title='二维码生成' />
      <div className={styles.box}>
        <Input onChange={(e) => { setText(e) }} style={{ width: '90%', margin: '20px auto' }} clearable placeholder='输入文本或链接' />
        <p className={styles.tip}>注:链接必须带http或https</p>
        <Input onChange={(e) => { setSize(e) }} defaultValue={size} min={1} style={{ width: '90%', margin: '20px auto' }} type='number' clearable placeholder='输入大小' />
        <Button block color='primary' onClick={submit}>生成</Button>
      </div>
    </div>
  )
}
